<template>
  <v-form>
    <v-row>
      <v-col cols="12" sm="6">
        <name-input v-model="form.firstname" 
          @validated="firstnameIsValid = true" 
          @not-validated="firstnameIsValid = false"
        />
      </v-col>
      <v-col cols="12" sm="6">
        <surname-input v-model="form.surname"
          @validated="surnameIsValid = true" 
          @not-validated="surnameIsValid = false"
        />
      </v-col>
      <v-col cols="12">
        <email-input v-model="form.email"
          @validated="emailIsValid = true" 
          @not-validated="emailIsValid = false"
        />
      </v-col>
      <v-col cols="12" sm="6">
        <username-input v-model="form.username" 
          @validated="usernameIsValid = true" 
          @not-validated="usernameIsValid = false"
        />
      </v-col>
      <v-col cols="12" sm="6">
        <birthdate-input v-model="form.birthdate"
          @validated="birthdateIsValid = true" 
          @not-validated="birthdateIsValid = false"
        />
      </v-col>
      <v-col cols="12">
        <password-input v-model="form.password" 
          @validated="passwordIsValid = true" 
          @not-validated="passwordIsValid = false"
        />
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
import NameInput from './components/NameInput.vue'
import SurnameInput from './components/SurnameInput.vue'
import EmailInput from './components/EmailInput.vue'
import UsernameInput from './components/UsernameInput.vue'
import BirthdateInput from './components/BirthdateInput.vue'
import PasswordInput from './components/PasswordInput.vue'

export default {
    name: 'RegisterPage-Form',
    components: { 
      NameInput,
      SurnameInput,
      EmailInput,
      UsernameInput,
      BirthdateInput,
      PasswordInput
    },
    props: {
      value: {
        type: Object,
        required: true
      }
    },
    data(){
      return {
        birthdateIsValid: false,
        firstnameIsValid: false,
        surnameIsValid: false,
        emailIsValid: false,
        usernameIsValid: false,
        passwordIsValid: false
      }
    },
    computed:{
      formState(){
        return this.firstnameIsValid && this.birthdateIsValid && this.surnameIsValid && this.emailIsValid && this.usernameIsValid && this.passwordIsValid
      },
      form:{
        get(){
          return this.value
        },
        set(value){
          this.$emit('input', value)
        }
      },
    },
    watch:{
      passwordIsValid(){
        this.form.formState = this.formState
        this.$emit('input', this.form)
      },
      formState(){
        this.form.formState = this.formState
        this.$emit('input', this.form)
      },
      'form.firstname': {
        handler(){
          this.form.formState = this.formState
          this.$emit('input', this.form)
        }
      },
      'form.surname': {
        handler(){
          this.form.formState = this.formState
          this.$emit('input', this.form)
        }
      },
      'form.email': {
        handler(){
          this.form.formState = this.formState
          this.$emit('input', this.form)
        }
      },
      'form.username': {
        handler(){
          this.form.formState = this.formState
          this.$emit('input', this.form)
        }
      },
      'form.birthdate': {
        handler(){
          this.form.formState = this.formState
          this.$emit('input', this.form)
        }
      },
      'form.password': {
        handler(){
          this.form.formState = this.formState
          this.$emit('input', this.form)
        }
      },
    }
}
</script>

