import Vue from "vue";
import VueI18n from 'vue-i18n'
import merge from 'deepmerge'

import {i18nMessages as i18nPages} from './pages'
import {i18nMessages as i18nGenerals} from './components/generals'


const messages = merge.all([
  i18nPages,
  i18nGenerals
])

Vue.use(VueI18n)

const i18n = new VueI18n({
    locale: 'es',
    messages,
})

export default i18n