<template>
    <v-form v-model="formValue">
        <v-text-field
            append-icon="badge"
            v-model="surname"
            label="Apellido"
            hint="¿Cuál es tu apellido?"
            :rules="rules"
        ></v-text-field>
    </v-form>
</template>

<script>
export default {
    name: 'SurnameInput',
    props: {
        value: {
            type: String,
            required: true
        }
    },
    data(){
        return{
            formValue: false,
            rules: [
                value => !!value || 'Esta información es necesaria.',
                value => value.length < 80 || 'Ingrese un apellido válido.'
            ]
        }
    },
    computed: {
        surname: {
            get(){
                return this.value
            },
            set(value){
                if(this.formValue) this.$emit('validated')
                if(!this.formValue) this.$emit('not-validated') 
                this.$emit('input', value)
            }
        }
    }
}
</script>

