<template>
    <v-form v-model="formValue">
        <v-text-field
            append-icon="person"
            v-model="username"
            label="Usuario"
            hint="¿Cómo quieres visualizarte en la app?"
            :rules="rules"
        ></v-text-field>
    </v-form>
</template>

<script>
export default {
    name: 'UsernameInput',
    props: {
        value: {
            type: String,
            required: true
        }
    },
    data(){
        return{
            formValue: false,
            rules: [
                value => !!value || 'Esta información es necesaria.',
                value => value.length > 3 || 'Tu nombre de usuario es demaciado corto.'
            ]
        }
    },
    computed: {
        username: {
            get(){
                return this.value
            },
            set(value){
                if(this.formValue) this.$emit('validated')
                if(!this.formValue) this.$emit('not-validated') 
                this.$emit('input', value)
            }
        }
    }
}
</script>

