import graphqlClient from '../apollo'

class Provider {
    createUser({googleAuthenticated = false, firstname, surname, email, username, birthdate, password}){
        return graphqlClient.mutate({
            mutation: require('./gql/User/createUser.graphql'),
            variables: {googleAuthenticated, firstname, surname, email, username, birthdate, password}
        })
    }
}

export default new Provider()