<template>
    <v-row>
        <v-col cols="12" sm="12">
        <v-form v-model="firstFormValue">
            <v-text-field 
                append-icon="key"
                label="Ingrese su contraseña"
                v-model="firstPassword" 
                :rules="firstPasswordRules"
                :type="showFirstPassword ? 'text': 'password'"
                :prepend-icon="showFirstPassword ? 'mdi-eye': 'mdi-eye-off'"
                @click:prepend="showFirstPassword = !showFirstPassword"
            />
        </v-form>
        </v-col>
        <v-col cols="12" sm="12">
        <v-form v-model="secondFormValue">
            <v-text-field
                append-icon="key"
                label="Vuelva a ingresar su contraseña"
                v-model="secondPassword" 
                :rules="secondPasswordRules"
                :type="showSecondPassword ? 'text': 'password'"
                :prepend-icon="showSecondPassword ? 'mdi-eye': 'mdi-eye-off'"
                @click:prepend="showSecondPassword = !showSecondPassword"
            />
        </v-form>
        </v-col>
    </v-row>
</template>

<script>
const regexp_password = RegExp(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$^&*()_-]).{8,18}$/) // eslint-disable-line

export default {
    name: 'PasswordInput',
    props:{
        value: {
            type: String,
            required: true
        }
    },
    data(){
        return {
            firstFormValue: false,
            secondFormValue: false,
            showSecondPassword: false,
            showFirstPassword: false,
            secondPassword: '',
            firstPasswordRules: [
                value => !!value || 'La contraseña no debe estar vacía.',
                value => value.length >=8 || 'El minimo de caracteres permitidos es de 8.',
                value => value.length <=18 || 'Estás superando el máximo permitido.',
                value => regexp_password.test(value) || 'La contraseña debe contener almentos una letra minuscula, una letra mayuscula, un numero y un simbolo: (!@#$^&*()_-.)',
            ],
            secondPasswordRules: [
                value => !!value || 'Vuelve a ingresar tu contraseña.',
                value => this.firstPassword == value || 'Las contraseñas no coinciden.'
            ]
        }
    },
    computed:{
        firstPassword:{
            get(){
                return this.value
            },
            set(value){
                if(this.firstFormValue && this.secondFormValue) this.$emit('validated')
                if(!this.firstFormValue || !this.secondFormValue) this.$emit('not-validated')
                this.$emit('input', value)
            }
        }
    },
    watch:{
        firstPassword(){
            if(this.firstFormValue && this.secondFormValue) this.$emit('validated')
            if(!this.firstFormValue || !this.secondFormValue) this.$emit('not-validated')
            if(this.firstPassword != this.secondPassword) this.$emit('not-validated')
        },
        secondPassword(){
            if(this.firstFormValue && this.secondFormValue) this.$emit('validated')
            if(!this.firstFormValue || !this.secondFormValue) this.$emit('not-validated')
            if(this.firstPassword != this.secondPassword) this.$emit('not-validated')
        },
        secondFormValue(){
            if(this.firstFormValue && this.secondFormValue) this.$emit('validated')
            if(!this.firstFormValue || !this.secondFormValue) this.$emit('not-validated')
        },
        firstFormValue(){
            if(this.firstFormValue && this.secondFormValue) this.$emit('validated')
            if(!this.firstFormValue || !this.secondFormValue) this.$emit('not-validated')
        }
    }
}
</script>

