<template>
  <v-btn 
    block 
    color="primary" 
    large 
    @click="registerUser"
    :loading="loading"
    :disabled="!disabled"
  >
  Registrate Ahora
  </v-btn>
</template>

<script>
import provider from '@/providers/Providers'

export default {
    name: 'RegisterButton',
    props: {
      form: {
        type: Object,
        required: true
      },
      disabled: {
        type: Boolean,
        required: false
      }
    },
    data(){
      return{
        loading: false
      }
    },
    computed:{
      user(){
        console.log(this.form)
        const {email, birthdate, firstname, surname, username, password} = this.form
        return {email, birthdate, firstname, surname, username, password}
      }
    },
    methods: {
      validate(){
        return true
      },
      registerUser(){
        this.loading = true
        provider.createUser(this.user).then(response => {
          console.log('Nuevo usuario creado: ', response.data.createUser)
        }).catch(error => {
          console.error('Ocurrió un error al crear el usuario: ', error)
        }).finally(() => {
          this.loading = false
        })
      }
    }
}
</script>