<template>
    <v-navigation-drawer
        app
        temporary
        right
        v-model="navigationDrawerState"
        color="primary"
        v-click-outside="clickOutSide"
        width="400"
        class="ma-0 pa-2"
    >
        <v-list>
            <sub-menu />
        </v-list>
    </v-navigation-drawer>
</template>

<script>
    import { SubMenu } from './components'

    export default {
        name: 'GeneralSessionMenu',
        components: {
            SubMenu
        },
        props: {
            value: {
                type: Boolean,
                required: true
            }
        },
        computed: {
            navigationDrawerState:{
                get(){
                    return this.value
                },
                set(value){
                    if(value) this.$emit('input', value)
                }
            }
        },
        methods:{
            clickOutSide(){
                if(this.navigationDrawerState == true) this.$emit('input', false)
            }
        }
    }
</script>