<template>
    <v-app-bar app color="primary" height="80" class="px-4">
        <active-menu-button v-model="stateMenu"/>
        <v-spacer></v-spacer>
        <v-card-title>Eva Muñoz</v-card-title>
        <active-session-button v-model="stateSessionMenu" />
    </v-app-bar>
</template>

<script>
    import ActiveMenuButton from './components/ActiveMenuButton.vue'
    import ActiveSessionButton from './components/ActiveSessionButton.vue'

    export default {
        name: 'GeneralNavigationBar',
        props: {
            menuState: {
                type: Boolean,
                required: true
            },
            sessionMenuState: {
                type: Boolean,
                required: true
            }
        },
        components: {
            ActiveMenuButton,
            ActiveSessionButton
        },
        computed: {
            stateSessionMenu: {
                get(){
                    return this.sessionMenuState
                },
                set(value){
                    setTimeout(()=> {
                        if(value) this.$emit('session-menu-active')
                        if(!value) this.$emit('session-menu-inactive')
                    }, 1)
                }
            },
            stateMenu:{
                get(){
                    return this.menuState
                },
                set(value){
                    setTimeout(() => {
                        if(value) this.$emit('menu-active')
                        if(!value) this.$emit('menu-inactive')
                    }, 1);
                }
            }
        },
    }
</script>