<template>
    <v-form v-model="formValue">
        <v-text-field
            append-icon="email"
            v-model="email"
            label="Email"
            hint="Te enviaremos un mail para concluir con el registro."
            :rules="rules"
        ></v-text-field>
    </v-form>
</template>

<script>
const emailValidationRegex = RegExp(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/) // eslint-disable-line

export default {
    name: 'EmailInput',
    props: {
        value: {
            type: String,
            required: true
        }
    },
    data(){
        return{
            formValue: false,
            rules:[
                value => !!value || 'Esta información es necesaria',
                value => emailValidationRegex.test(value) || 'Debe ingresar un email valido'
            ]
        }
    },
    computed:{
        email:{
            get(){
                return this.value
            },
            set(value){
                if(this.formValue) this.$emit('validated')
                if(!this.formValue) this.$emit('not-validated') 
                this.$emit('input', value)
            }
        }
    }
}
</script>

