<template>
    <v-form v-model="formValue">
        <v-text-field
            append-icon="badge"
            v-model="firstName"
            label="Nombre"
            hint="¿Cuál es tu nombre?"
            :rules="rules"
        ></v-text-field>
    </v-form>
</template>

<script>
export default {
    name: 'NameInput',
    props:{
        value: {
            type: String,
            required: true
        }
    },
    data(){
        return{
            formValue: false,
            rules: [
                value => !!value || 'Esta información es necesaria.',
                value => value.length < 40 || 'Ingrese un Nombre válido'
            ]
        }
    },
    computed: {
        firstName: {
            get(){
                return this.value
            },
            set(value){
                if(this.formValue) this.$emit('validated')
                if(!this.formValue) this.$emit('not-validated') 
                this.$emit('input', value)
            }
        }
    }
}
</script>

