<template>
    <v-menu
        v-model="menu"
        :close-on-content-click="false"
        :nudge-right="40"
        transition="scale-transition"
        offset-y
        min-width="auto"
    >
        <template v-slot:activator="{ on, attrs }">
            <v-form v-model="formValue">
                <v-text-field
                v-model="date"
                label="Fecha de nacimiento"
                append-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                :rules="rules"
                ></v-text-field>
            </v-form>
        </template>
        <v-date-picker
            :allowed-dates="allowedDates"
            v-model="date"
            @input="menu = false"
        ></v-date-picker>
    </v-menu>
</template>

<script>
export default {
    name:'DatePicker',
    props:{
        value: {
            type: String,
            required: true
        },
        rules: {
            type: Array,
            required: true
        },
        allowedDates: {
            type: Function,
            required: true
        }
    },
    data(){
        return{
            formValue: false,
            menu: false
        }
    },
    computed: {
        date:{
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    },
    watch:{
        formValue(value){
            if(value) this.$emit('validated')
            if(!value) this.$emit('not-validated')
        }
    }
}
</script>

