<template>
    <date-picker 
        v-model="date" 
        :rules="rules" 
        :allowedDates="allowedDates" 
        @validated="$emit('validated')" 
        @not-validated="$emit('not-validated')"
    />
</template>

<script>
import dayjs from 'dayjs'
import DatePicker from "./DatePicker.vue"

export default{
    name: 'BirthdateInput',
    components: { 
        DatePicker 
    },
    props: {
        value: {
            type: String,
            required: true
        }
    },
    data(){
        return {
            rules: [
                value => !!value || 'Esta información es necesaria.',
            ],
            allowedDates: (value) => {
                return dayjs().subtract(21, 'year').isAfter(dayjs(value))
            }
        }
    },
    computed: {
        date: {
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    }
}
</script>

